/* v2.1.3
Lyttle Reset ~ Recommended to use with Lyttle Defaults:
https://github.com/Stualyttle/LyttleTools/blob/main/CSS/defaults.css
*/

*,
*::before,
*::after {
  box-sizing: border-box;
  content: "";
  content: none;
  margin: 0;
  padding: 0;
  word-wrap: break-word;
}

html:focus-within {
  scroll-behavior: smooth;
}

@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
  }

  html:focus-within {
    scroll-behavior: auto;
  }
}

// Add ',#root, #root > *' for react support.
body,
#root,
#root > * {
  line-height: 1;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
}

b,
strong {
  font-weight: bolder;
}

blockquote,
q {
  quotes: none;
}

button,
[type="button"],
[type="submit"] {
  -webkit-appearance: button;
}

img,
picture {
  border-style: none;
  display: block;
  max-width: 100%;
  max-height: 100%;
}

button,
input,
optgroup,
select,
textarea,
::placeholder {
  background: none;
  border: none;
  color: inherit;
  font: inherit;
  opacity: 1;
  outline: none;
  overflow: visible;
  text-transform: inherit;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
  border: 0;
  color: inherit;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  text-decoration: inherit;
  vertical-align: baseline;
}